import React, { Component } from "react";
import { BrowserRouter, Route, Switch, Redirect } from "react-router-dom";
import Register from "./pages/Register/Register";
import Login from "./pages/Login/Login";
import Home from "./pages/Home/Home";
import AddEstate from "./pages/addEstate/addEstate";

class Routes extends Component {
  constructor() {
    super();
    this.state = {
      redirectToLogin: false,
    };
  }

  componentDidMount = async () => {
    const usertoken = await window.sessionStorage.getItem("usertoken");
    if (!usertoken) {
      this.setState({ redirectToLogin: true });
    }
  };

  render() {
    const { redirectToLogin } = this.state;

    return (
      <BrowserRouter>
        <Switch>
          <Route path={"/oqod4/Create"} component={AddEstate} />
          <Route path={"/oqod4/Home"} component={Home} />
          <Route path={"/oqod4/login"} component={Login} />
          <Route path={"/oqod4"} component={Register} />
        </Switch>
        {/* {redirectToLogin && <Redirect to={"/"} />} */}
      </BrowserRouter>
    );
  }
}

export default Routes;
